import i18n from 'i18next'
import XHR from 'i18next-xhr-backend';

i18n.use(XHR).init({
    fallbackLng: 'en',
    debug: false,
    detection: {
        order: ['localstorage', 'cookie'],
        cache: ['cookie']
    },
    interpolation: {
        escapeValue: false
    },
    backend: {
        loadPath: '/resume/locales/{{lng}}/{{ns}}.json',
    },
});

export default i18n